
div {
  box-sizing : border-box;
  background: rgb(255, 254, 221);
}
.black-nav {
  width: 100%;
  background-color: black;
  color: rgb(255, 254, 221);
  padding: 20px;
  display: inline-block;
  vertical-align: middle;
  height: 100%;
  text-align: center;
}
.posts{
  background-color: white;
  margin-left: auto; margin-right: auto;
  margin-top:30px;
  width: 500px;
  padding: 0px 10px 10px 10px;
  border:rgb(0, 0, 97) solid 2px;
  border-radius: 10px;
}
.list {
  background-color: white;

  text-align : left;
  padding-left : 20px;
  height: fit-content;
  padding: 10px;
  border-bottom : 1px solid grey;
}
.modal{
  margin-top : 20px;
  padding : 20px;
  background : #eee;
  text-align : left;
}

.mymodal{
  margin-top : 20px;
  padding : 20px;
  background : grey;
  text-align : left;
}